<template>
    <div class="row">
        <div class="col-md-12">
            <div class="card">
                <div class="card-header">
                    <div class="row d-flex align-items-center">
                        <div class="col-md-12 d-flex align-items-center">
                            <h5>Expediente</h5>
                            <div class="form-check form-switch ml-auto">
                                <input class="form-check-input" type="checkbox" role="switch"
                                    id="flexSwitchCheckChecked" :checked="isMultiple == '0' ? false : true"
                                    @change="mtdChangeMultiple" />
                                <label class="form-check-label" for="flexSwitchCheckChecked">{{ isMultiple == '1' ?
                                    "Multiple Demandantes" : "Un Solo Demandante" }}</label>
                            </div>
                        </div>
                        <!-- <div class="col-lg-6 col-md-auto col-sm-12">
                            <div class="d-flex excel-div">
                                <input type="file" ref="fileInput" @change="onFileChange" class="form-control excel-input"
                                    style="height: 100%; width:72.5% ; padding-top: 9px; padding-bottom: 9px;"
                                    accept=".xlsx, .xls" />
                                <button class="btn btn-success btn-lg btn-block ml-auto rounded excel-btn"
                                    @click="uploadFile()">Cargar
                                    Excel</button>
                            </div>
                        </div> -->
                    </div>
                </div>
                <div class="card-body">
                    <div class="row">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="form-group">
                                <label class="form-label">N de Expediente</label>
                                <input type="text" class="form-control" placeholder="5670-2017-0-1708-JM-LA-01"
                                    v-model="datosgenerales.exp_numero" :class="{ 'has-error': errores.exp_numero }" />
                                <span class="error-message" v-if="errores.exp_numero">Ingrese un numero de
                                    expediente.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Fecha de Inicio</label>
                                <input class="form-control" type="date" v-model="datosgenerales.exp_fecha_inicio"
                                    @input="validarCampo('exp_fecha_inicio')"
                                    :class="{ 'has-error': errores.exp_fecha_inicio }" />
                                <span class="error-message" v-if="errores.exp_fecha_inicio">Ingrese una fecha de
                                    inicio.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Pretensin</label>
                                <v-select :options="pretenciones.filter(pretension => pretension.type_id === 1)"
                                    :reduce="(pretension) => pretension.pre_id" label="pre_nombre"
                                    placeholder="-- Seleccionar" v-model="datosgenerales.exp_pretencion"
                                    @input="validarCampo('exp_pretencion')"
                                    :class="{ 'has-error': errores.exp_pretencion }" />
                                <span class="error-message" v-if="errores.exp_pretencion">Seleccione una
                                    Pretension.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Monto de Pretensin</label>
                                <input type="text" class="form-control" placeholder="5500.00"
                                    v-model="datosgenerales.exp_monto_pretencion"
                                    :class="{ 'has-error': errores.exp_monto_pretencion }"
                                    @keydown="preventNonNumericInput($event)"
                                    @input="validatePositiveNumber(datosgenerales, 'exp_monto_pretencion')" />
                                <span class="error-message" v-if="errores.exp_monto_pretencion">Ingrese un monto de
                                    pretension.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Materia</label>
                                <v-select :options="materias.filter(materia => materia.type_id === 1)"
                                    :reduce="(materia) => materia.mat_id" label="mat_nombre"
                                    placeholder="-- Seleccionar" v-model="datosgenerales.exp_materia"
                                    @input="validarCampo('exp_materia')"
                                    :class="{ 'has-error': errores.exp_materia }" />
                                <span class="error-message" v-if="errores.exp_materia">Seleccione una Materia.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Distrito Judicial</label>
                                <v-select :options="distritos" :reduce="(distrito) => distrito.judis_id"
                                    label="judis_nombre" placeholder="-- Seleccionar"
                                    v-model="datosgenerales.exp_distrito_judicial" @input="cargarinstancias"
                                    :class="{ 'has-error': errores.exp_distrito_judicial }" />
                                <span class="error-message" v-if="errores.exp_distrito_judicial">Seleccione un Distrito
                                    Judicial.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Instancia</label>
                                <v-select :options="instancia.filter(instancia => instancia.type_id === 1)"
                                    :reduce="(instancia) => instancia.ins_id" label="ins_nombre"
                                    placeholder="-- Seleccionar" v-model="datosgenerales.exp_instancia"
                                    @input="cargarespecialidades" :class="{ 'has-error': errores.exp_instancia }" />
                                <span class="error-message" v-if="errores.exp_instancia">Seleccione una
                                    Instancia.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label">Especialidad</label>
                                <v-select :options="especialidad.filter(especialidad => especialidad.type_id === 1)"
                                    :reduce="(especialidad) => especialidad.esp_id" label="esp_nombre"
                                    placeholder="-- Seleccionar" v-model="datosgenerales.exp_especialidad"
                                    @input="validarCampo('exp_especialidad')"
                                    :class="{ 'has-error': errores.exp_especialidad }" />
                                <span class="error-message" v-if="errores.exp_especialidad">Seleccione una
                                    Especialidad.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label mb-1">rgano Jurisdiccional</label>
                                <v-select :options="juzgados.filter(juzgado => juzgado.type_id === 1)"
                                    :reduce="(juzgado) => juzgado.co_id" label="co_nombre" placeholder="-- Seleccionar"
                                    v-model="datosgenerales.exp_juzgado" @input="validarCampo('exp_juzgado')"
                                    :class="{ 'has-error': errores.exp_juzgado }" />
                                <span class="error-message" v-if="errores.exp_juzgado">Seleccione un Organo
                                    Jurisdiccional.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4">
                            <div class="form-group">
                                <label class="form-label mb-1">Estado</label>
                                <select class="form-control" v-model="datosgenerales.exp_estado_proceso"
                                    :class="{ 'has-error': errores.exp_estado_proceso }"
                                    @change="validarCampo('exp_estado_proceso')">
                                    <option value="" disabled selected class="py-5">-- Seleccionar</option>
                                    <option value="EN TRAMITE">En Trmite</option>
                                    <option value="EN EJECUCION">En Ejecucin</option>
                                    <option value="ARCHIVADO">Archivado</option>
                                </select>
                                <span class="error-message" v-if="errores.exp_estado_proceso">Selecione un estado para
                                    el
                                    Expediente.</span>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4" v-if="status">
                            <div class="form-group">
                                <label class="form-label" for="MontoPretencion">Monto de Ejecucin 1</label>
                                <input v-model="datosgenerales.exp_monto_ejecucion1" type="text" class="form-control"
                                    id="MontoPretencion" placeholder="S/ 5500.00"
                                    @keydown="preventNonNumericInput($event)"
                                    @input="validatePositiveNumber(datosgenerales, 'exp_monto_ejecucion1')">
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4" v-if="status">
                            <div class="form-group">
                                <label class="form-label" for="MontoConsentido">Monto de Ejecucin 2</label>
                                <input v-model="datosgenerales.exp_monto_ejecucion2" type="text" class="form-control"
                                    id="MontoConsentido" placeholder="S/ 5000.00"
                                    @keydown="preventNonNumericInput($event)"
                                    @input="validatePositiveNumber(datosgenerales, 'exp_monto_ejecucion2')" />
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4" v-if="status">
                            <div class="form-group">
                                <label class="form-label" for="Costos">Inters 1</label>
                                <input v-model="datosgenerales.exp_interes1" type="text" class="form-control" id="int1"
                                    placeholder="S/ 450.00" @keydown="preventNonNumericInput($event)"
                                    @input="validatePositiveNumber(datosgenerales, 'exp_interes1')" />
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4" v-if="status">
                            <div class="form-group">
                                <label class="form-label" for="MontoPretencion">Inters 2</label>
                                <input v-model="datosgenerales.exp_interes2" type="text" class="form-control" id="int2"
                                    placeholder="S/ 500.00" @keydown="preventNonNumericInput($event)"
                                    @input="validatePositiveNumber(datosgenerales, 'exp_interes2')" />
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4" v-if="status">
                            <div class="form-group">
                                <label class="form-label" for="MontoPretencion">Costos</label>
                                <input v-model="datosgenerales.exp_costos" type="text" class="form-control" id="Costos"
                                    placeholder="S/ 2500.00" @keydown="preventNonNumericInput($event)"
                                    @input="validatePositiveNumber(datosgenerales, 'exp_costos')" />
                            </div>
                        </div>
                    </div>
                </div>
                <div class="card-footer d-flex justify-content-end">
                    <button class="btn btn-primary" type="button" @click="siguienteParte">
                        Siguiente <i data-feather="arrow-right"></i>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import feather from 'feather-icons';
import { getData, postData } from '../../../utils/fetchData';
import { showLoadingAlert, closeLoadingAlert, warnignAlert, successAlert, errorAlert } from '../../../utils/loader';
import Swal from 'sweetalert2';
export default {
    name: "RegistroGeneral",
    props: {
        exp: {},
        ismultiple: '',
    },
    created() {
        this.isMultiple = this.ismultiple;
        this.listarMaterias();
        this.listarPretensiones();
        this.datosgenerales = this.exp;
        this.cargarinstancias();
        this.cargarespecialidades();
        this.cargarjuzgados();
        this.cargardistritos();
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    computed: {
        status() {
            return ['EN EJECUCION', 'ARCHIVADO'].includes(this.datosgenerales.exp_estado_proceso);
        }
    },
    data() {
        return {
            isMultiple: "",
            distritos: [],
            instancia: [],
            especialidad: [],
            materias: [],
            pretenciones: [],
            distritos: [],
            juzgados: [],
            datosgenerales: {
                exp_numero: "",
                exp_fecha_inicio: "",
                exp_pretencion: "",
                exp_materia: "",
                exp_distrito_judicial: "",
                exp_instancia: "",
                exp_especialidad: "",
                exp_monto_pretencion: "",
                exp_juzgado: "",
                exp_estado_proceso: "",
                exp_costos: "",
                exp_monto_ejecucion1: "",
                exp_monto_ejecucion2: "",
                exp_interes1: "",
                exp_interes2: "",

            },
            selectedFile: null,
            fileError: "",

            errores: {
                file: false,
                exp_numero: false,
                exp_fecha_inicio: false,
                exp_pretencion: false,
                exp_monto_pretencion: false,
                exp_materia: false,
                exp_distrito_judicial: false,
                exp_instancia: false,
                exp_especialidad: false,
                exp_juzgado: false,
                exp_estado_proceso: false
            },
        };
    },
    methods: {
        formatoNumero() {
            // Obtener el valor actual del campo
            let numero = this.datosgenerales.exp_numero;

            // Eliminar caracteres no deseados
            numero = numero.replace(/[^\dA-Z]/g, '');

            // Aplicar formato (5670-2017-0-1708-JM-LA-01)
            let nuevoNumero = '';
            for (let i = 0; i < numero.length; i++) {
                if (i === 4 || i === 9 || i === 11 || i === 15 || i === 18 || i === 21) {
                    nuevoNumero += '-';
                }
                nuevoNumero += numero[i];
            }

            // Actualizar el valor del campo con el formato aplicado
            this.datosgenerales.exp_numero = nuevoNumero;
        },
        preventNonNumericInput(event) {
            // Verificar si la tecla presionada no es un nmero o un punto
            if (!/^\d$/.test(event.key) && event.key !== '.' && event.key !== 'Backspace' && event.key !== 'Delete') {
                event.preventDefault();
            }
        },
        validatePositiveNumber(dataObject, fieldName) {
            let value = dataObject[fieldName];
            // Validar si el valor no es un nmero positivo (incluido el punto)
            if (value !== '' && (isNaN(value) || parseFloat(value) <= 0)) {
                dataObject[fieldName] = '';
            }
        },
        async listarMaterias() {
            try {
                const result = await getData('subject', this);
                this.materias = result.data;
            } catch (error) {
                this.$handleApiError(error, 'listar materias');
            }
        },
        async listarPretensiones() {
            try {
                const result = await getData('claim', this);
                this.pretenciones = result.data;
                console.log(this.pretenciones);
            } catch (error) {
                this.$handleApiError(error, 'listar pretensiones');
            }
        },
        async cargardistritos() {
            try {
                const result = await getData('judicialdistrict', this);
                this.distritos = result.data;
            } catch (error) {
                this.$handleApiError(error, 'listar distritos judiciales');
            }
        },
        async cargarjuzgados() {
            if (this.datosgenerales.exp_distrito_judicial) {
                try {
                    const params = {
                        judis_id: this.datosgenerales.exp_distrito_judicial
                    }
                    const result = await postData('juzgado', this, params);
                    this.juzgados = result.data;
                } catch (error) {
                    this.$handleApiError(error, 'listar organos jurisdiccionales');
                }
            }
        },
        async cargarinstancias() {
            if (this.datosgenerales.exp_distrito_judicial) {
                try {
                    const result = await getData('instance', this);
                    this.instancia = result.data;
                    this.cargarjuzgados();
                } catch (error) {
                    this.$handleApiError(error, 'listar instancias');
                }
            }
        },

        async cargarespecialidades() {
            if (this.datosgenerales.exp_distrito_judicial) {
                try {
                    const result = await getData('specialty', this);
                    this.especialidad = result.data;
                } catch (error) {
                    this.$handleApiError(error, 'listar especialidades');
                }
            }
        },

        getJuzgadoNombre(juzgado) {
            return juzgado.co_nombre ? juzgado.co_nombre.toUpperCase() : '';
        },

        onFileChange(event) {
            this.selectedFile = event.target.files[0];
        },

        uploadFile() {
            if (!this.selectedFile) {
                Swal.fire({
                    icon: 'warning',
                    title: '',
                    text: 'Tiene que seleccionar un archivo para cargar datos.',
                });
                return;
            }
            let loadingAlert;
            loadingAlert = Swal.fire({
                title: " Cargando Datos",
                text: "Por favor espera...",
                showConfirmButton: false,
                allowOutsideClick: false,
                didOpen: () => Swal.showLoading()
            });

            const formData = new FormData();
            formData.append('excel', this.selectedFile);

            this.$axios.post(
                `${this.$store.getters.get__url}/excel/cargar`,
                formData,
                {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                        'Authorization': `Bearer ${this.$store.getters.get__token}`
                    },
                    params: this.archivo
                }
            )
                .then(response => {
                    console.log(response.data);
                    if (loadingAlert) loadingAlert.close();
                    Swal.fire({
                        title: 'Datos Cargados Correctamente',
                        text: `Las filas que tuvieron error  son: ${response.data.data.join(', ')}`,
                        icon: 'success',
                        allowOutsideClick: false,
                        showCancelButton: false,
                        confirmButtonColor: '#3085d6',
                        confirmButtonText: 'OK',
                    }).then((result) => {
                        if (result.isConfirmed) {
                            this.$refs.fileInput.value = null;
                            this.selectedFile = null;
                            this.$router.push({ path: '/sisge/expediente/busqueda' });
                        }
                    });
                })
                .catch(error => {
                    Swal.fire({
                        icon: 'error',
                        title: 'Error al cargar datos',
                        text: `Ha ocurrido un error al cargar el archivo:${error.response.data.message}`,
                    });
                    if (loadingAlert) loadingAlert.close();
                    console.error(error);
                });
        },
        validarCampo(campo) {
            this.errores[campo] = this.datosgenerales[campo] === "";
        },

        validarCampos() {
            Object.keys(this.errores).forEach(propiedadError => {
                this.validarCampo(propiedadError, propiedadError);
            });

            return !Object.values(this.errores).some(error => error);
        },
        siguienteParte() {
            if (this.validarCampos()) {
                this.$emit("guardardatosgenerales", this.datosgenerales);
                this.$emit("guardarmultiple", this.isMultiple);
                this.$emit("siguienteParte");
            } else {
                console.log('error');
            }
        },
        mtdChangeMultiple() {
            this.isMultiple = this.isMultiple == "1" ? "0" : "1";
        },
    }
};
</script>

<style scoped>
.currency-symbol {
    position: absolute;
    padding: 10px 20px;
    background-color: transparent;
}

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    /* padding-left: 15px; */
    color: #EA4D4D;
    font-size: 12px;
}

@media only screen and (max-width: 600px) {
    .excel-div {
        display: block !important;
    }

    .excel-input {
        width: 100% !important;
        margin-top: 1em;
        margin-bottom: 1em;
    }

    .excel-btn {
        width: 100%;
    }
}
</style>
